<template>
  <div>
    <el-input placeholder="Search" v-model="search" style="width: 200px; margin: 10px 10px 10px 0"></el-input>
    <el-select v-model="select" filterable placeholder="Select">
      <el-option
        :key="-1"
        label="All Countries"
        value="All Countries">
      </el-option>
      <el-option
        v-for="el in countriesForSelect"
        :key="el.value"
        :label="el.label"
        :value="el.value">
      </el-option>
    </el-select>
    <el-button type="primary" @click="$router.push('logs')">Logs</el-button>
    <el-table
      :data="paginated"
      border
      style="width: 100%">
      <el-table-column
        label="Publication"
        width="120">
        <template slot-scope="scope">
          {{ getDate(scope.row.publicationDate.seconds) }}
        </template>
      </el-table-column>
      <el-table-column
        prop="country"
        label="Country"
        width="120">
      </el-table-column>
      <el-table-column
        prop="title"
        label="Title">
        <template slot-scope="scope">
          <a href="" @click.prevent="goTo(scope.row)">{{ scope.row.title }}</a>
        </template>
      </el-table-column>
      <el-table-column
        prop="contact"
        label="Contact"
        width="220">
      </el-table-column>
      <el-table-column
        prop="source"
        label="source">
        <template slot-scope="scope">
          <a :href="scope.row.source" target="_blank">{{scope.row.source}}</a>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      :current-page.sync="currentPage"
      background
      layout="total, sizes, prev, pager, next"
      :page-sizes="[5, 10, 20, 50, 100]"
      :page-size="pageSize"
      :total="filtered.length">
    </el-pagination>
  </div>
</template>

<script>
import { db } from '../main'

export default {
  name: 'AllJobs',
  data () {
    return {
      jobs: [],
      search: '',
      select: 'All Countries',
      currentPage: 1,
      pageSize: 10,
      countries: [
        {
          value: 'aaa',
          label: 'aaa'
        },
        {
          value: 'bbb',
          label: 'bbb'
        }
      ]
    }
  },
  computed: {
    filtered () {
      if (this.select !== 'All Countries') {
        return this.jobs
          .filter(item => item.country === this.select)
          .filter(item => {
            return item.title.toLowerCase().indexOf(this.search.toLowerCase()) !== -1 ||
              item.desc.toLowerCase().indexOf(this.search.toLowerCase()) !== -1
          })
      } else {
        return this.jobs
          .filter(item => {
            return item.title.toLowerCase().indexOf(this.search.toLowerCase()) !== -1 ||
              item.desc.toLowerCase().indexOf(this.search.toLowerCase()) !== -1
          })
      }
    },
    paginated () {
      return this.filtered.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
    },
    countriesForSelect () {
      return this.jobs.reduce((r, a) => {
        if (r.indexOf(a.country) === -1) {
          r.push(a.country)
        }
        return r
      }, [])
        .map(el => {
          return {
            label: el,
            value: el
          }
        })
    }
  },
  firestore () {
    return {
      jobs: db.collection('jobs').orderBy('publicationDate', 'desc')
    }
  },
  methods: {
    getDate (date) {
      return new Date(date * 1000).toLocaleDateString('ru-ru')
    },
    goTo (data) {
      this.$emit('jobData', data)
      this.$router.push({ name: 'job' })
    },
    handleSizeChange (val) {
      this.pageSize = val
    }
  }
}
</script>

<style lang="scss">
a {
  color: #42b983;
}
.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #42b983!important;
  color: #FFF;
}
.el-pagination.is-background {
  /*text-align: center;*/
  margin-top: 20px;
  margin-left: 20px;
}
</style>
